const commentsUrl = 'https://us-central1-involvement-api.cloudfunctions.net/capstoneApi/apps/D2Xr8tRh2hyegwFvMhUF/comments';

export const postComment = async (id, username, comment) => {
  const response = await fetch(commentsUrl,
    {
      method: 'POST',
      body: JSON.stringify({
        item_id: id,
        username,
        comment,
      }),
      headers: {
        'Content-type': 'application/json; charset=UTF-8',
      },
    });
  const data = await response.text();
  return data;
};

// Get comments from the Api
export const getComments = async (id) => {
  const response = await fetch(`https://us-central1-involvement-api.cloudfunctions.net/capstoneApi/apps/D2Xr8tRh2hyegwFvMhUF/comments/?item_id=${id}`);
  const comments = await response.json();

  // Comments counter
  const ul = document.createElement('ul');
  const h2 = document.createElement('h2');

  if (comments.length) {
    h2.innerHTML = `Comments (${comments.length})`;
  } else {
    h2.innerHTML = 'Comments(0)';
  }
  ul.appendChild(h2);
  return comments;
};